@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,700);
@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App {
  text-align: center;
  background-color: #161319;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  font-family: 'Noto Sans', sans-serif;
}




.testbut{
  position: fixed;
  top: 50px;
  left: 46%;
  background-color: red;
  color: white;
}

.calendar{
  position: fixed;
  top: 50px;
  right: 70px;
  color: white;
}

.fade-enter {
  opacity: 0.2;
  -webkit-transform: translateX(1000px);
          transform: translateX(1000px)
}

.fade-enter.fade-enter-active {
  opacity: 1;
  transition: opacity 1500ms ease, -webkit-transform 1500ms ease;
  transition: opacity 1500ms ease, transform 1500ms ease;
  transition: opacity 1500ms ease, transform 1500ms ease, -webkit-transform 1500ms ease;
  -webkit-transform: translateX(0);
          transform: translateX(0)
}

.fade-exit {
  opacity: .1;
}

.fade-exit.fade-exit-active {
  opacity: 0.01;
  transition: opacity 600ms ease;
}

.selection{
  position: fixed;
  right: 50px;
  top: 50px;
  color:white;
  text-align: right;
}

.minititle{
  font-size: 13px;
  font-weight: 200;
}  

.content{
  font-size: 18px;
}

hr{
  border: 1px solid grey;
}
.menu{
    position: fixed;
    display: -webkit-flex;
    display: flex;
    background-color: #161319;
    height: 100vh;
    box-sizing: border-box;
    left: 20px;
    top: 0;
    width: 2010px;
    font-size: 13px;
    text-align: right;
    color:white;
    border-right: 1px solid rgb(141, 141, 141);
    -webkit-transform: translateX(-1920px);
            transform: translateX(-1920px);
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    padding: 15px;
    z-index: 500;
  }

  .menu.open{
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }


 
  .container{
    padding: 100px;
    padding-top: 60px;
    width: 1825px;
    box-sizing: border-box;
    color: white;
    text-align: left;
  }

  ul{
      list-style: none;
  }

  li{
    display: inline-block;
  }

  .minititle{
    font-size: 16px;
    font-weight: bolder
  }

  .columns3{
      display: -webkit-flex;
      display: flex;
      margin-top: 80px;
  }


  .column1{

      width: 44%;
      box-sizing: border-box;
      padding-top: 30px;
      font-family: Libre-Baskersville;
      font-weight: lighter;
      font-size: 58px;
  }

  .column{
      width: 28%;
      margin-top: 90px;
      box-sizing: border-box;
      padding: 15px;
      padding-top: 30px;
  }



  .sidebar{
    text-align: left;
    padding-right: 5px;
  }

  .sidebarvisible{
    box-sizing: border-box;
    border-left:1px solid rgba(141, 141, 141,0.4);
    width: 95px;
    padding-top: 123px;
    padding-left: 4px;
  }

  .burger{
    width: 30px;
    display: block;
    margin-top: 300px;
    margin-left: 15px;
    object-fit: contain;
  }


.story{
    border-top: 1px solid grey;
    text-align: right;
    margin-bottom: 40px;
}

.story h2{
    font-size: 22px;
    margin-bottom: 5px;
}

.story .link{
    text-decoration: none;
    border: none;
}
.story span{
    font-size: 13px;
    font-weight: bolder;
    color: rgb(0, 119, 255);
}
.testsvg{
    width: 1000px;
    margin:0 auto;
    padding-top: 20px;
    background: #161319;
    font-size: 12px;
    fill: white;
}

.buttons{
    position: absolute;
    top: 100;
    left: 100;
}
.testchart{
    width: 1600px;
    margin: 220px auto;
}
.testsvg{
    width: 1000px;
    margin:0 auto;
    padding-top: 20px;
    background: #161319;
    font-size: 12px;
    fill: white;
    height: 500px;
}

.buttons{
    position: absolute;
    top: 100;
    left: 100;
}
